<template>
    <yd-layout class="qy_xunjiandian">
        <navBar title="巡检点信息" slot="navbar"></navBar>
        <div style="display: flex;flex-wrap: wrap;justify-content:center" >
                <div class="list" v-for="item,index in data" :key="index"  @click="onPressMenu(item)">
                        <img class="icon" v-if="item.state == 0" src="../../assets/safe.png" alt="">
                        <img class="icon" v-if="item.state == 1" src="../../assets/warning.png" alt="">
                        <img class="icon" v-if="item.state == 2" src="../../assets/normal.png" alt="">
                        <h4>{{item.xjdmc}}</h4>
                        <p style="line-height: .6rem">{{item.name}}</p>
                        <p>{{item.time}}</p>
                </div>
            <div class="list" style="background-color: #f4f4f4"></div>
        </div>
    </yd-layout>

</template>

<script>
    import {api_xunjian_qiye} from '../../components/APIKeys'
    export default {
        name: "qy_xunjiandian",
        data:function () {
            return{
                // token:window.localStorage.getItem('token'),
                data:[],//获取的数据
                state:null,
                name:'',
                time:'',
                // list:[
                //     {id:1,state:'1',name:'热压机',people:'张三',time:'2018-11-29 09:30:15'},
                //     {id:2,state:'2',name:'消防设备',people:'张三',time:'2018-11-29 09:30:15'},
                //     {id:3,state:'3',name:'热压机',people:'张三',time:'2018-11-29 09:30:15'},
                // ]
            }
        },
        mounted(){
          this.doLoadData();
        },
        methods:{
            //获取数据
            doLoadData(){
                this.$showDialog();
                this.$post(api_xunjian_qiye, {}).then( res =>{
                    this.$showDialog(false);
                    if(res.errno == 0){
                        this.data = res.data;
                        for(let i = 0; i < this.data.length; i++){
                            if(this.data[i].xjjg == '有隐患'){
                                this.data[i].state = 1;
                                this.data[i].name = this.data[i].xjrxm;
                                this.data[i].time = this.data[i].xjsj;
                            }else if(this.data[i].xjjg == '无隐患'){
                                this.data[i].state = 0;
                                this.data[i].name = this.data[i].xjrxm;
                                this.data[i].time = this.data[i].xjsj;
                            }else{
                                this.data[i].state = 2;
                                this.data[i].name = '无';
                                this.data[i].time = '无';
                            }
                        }
                    }else{
                        this.$showError('获取信息失败');
                    }
                })
            },
            //点击跳转到详情页
            onPressMenu(item){
                window.localStorage.setItem('tiaomu',JSON.stringify(item.xunjiantiaomu));
                window.localStorage.setItem('name',item.xjdmc);
                this.$pushRoute('jianchatiaomu');
            }
        }
    }
</script>

<style scoped>
.icon{
    width: 1.5rem;
}
    .list{
        background-color: #ffffff;
        padding: .1rem;
        margin: .03rem;
        width: 48%;
        text-align: center;
    }

</style>



